/* resume.css */
.resume {
    font-family: Arial, sans-serif;
    margin: 20px auto;
    max-width: 600px;
  }
  
  .header {
    text-align: center;
  }
  
  .heading {
    margin-bottom: 10px;
    color: #0f1010; /* Highlight heading with color */
  }
  
  .separator {
    border: none;
    height: 2px;
    background-color: #3498db; /* Blue lines */
    margin: 10px 0;
  }
  
  .section {
    margin-bottom: 20px;
  }
  
  .content {
    list-style-type: none;
    padding: 0;
  }
  
  .content li {
    margin-bottom: 5px;
  }
  